.monaco-workbench > .part.titlebar {
  box-sizing: border-box;
  width: 100%;
  padding: 0 70px;
  overflow: hidden;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  user-select: none;
  zoom: 1; /* prevent zooming */
  line-height: 22px;
  height: 22px;
  display: flex;
}

.monaco-workbench > .part.titlebar > .titlebar-drag-region {
  top: 0;
  left: 0;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-app-region: drag;
}

.monaco-workbench > .part.titlebar > .window-title {
  flex: 0 1 auto;
  font-size: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: auto;
  margin-right: auto;
  zoom: 1; /* prevent zooming */
}

/* Windows/Linux: Rules for custom title (icon, window controls)  */

.monaco-workbench.windows > .part.titlebar,
.monaco-workbench.linux > .part.titlebar {
  padding: 0;
  height: 30px;
  line-height: 30px;
  justify-content: left;
  overflow: visible;
}

.monaco-workbench.windows > .part.titlebar > .window-title,
.monaco-workbench.linux > .part.titlebar > .window-title {
  cursor: default;
}

.monaco-workbench.linux > .part.titlebar > .window-title {
  font-size: inherit;
}

.monaco-workbench.windows > .part.titlebar > .resizer,
.monaco-workbench.linux > .part.titlebar > .resizer {
  -webkit-app-region: no-drag;
  position: absolute;
  top: 0;
  width: 100%;
  height: 20%;
}

.monaco-workbench.windows.fullscreen > .part.titlebar > .resizer,
.monaco-workbench.linux.fullscreen > .part.titlebar > .resizer {
  display: none;
}

.monaco-workbench > .part.titlebar > .window-appicon {
  width: 35px;
  height: 100%;
  position: relative;
  z-index: 99;

  background-repeat: no-repeat;
  background-position: center center;
  background-size: 16px;
  flex-shrink: 0;
}

.monaco-workbench.fullscreen > .part.titlebar > .window-appicon {
  display: none;
}

.monaco-workbench > .part.titlebar > .window-controls-container {
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  text-align: center;
  position: relative;
  z-index: 99;
  -webkit-app-region: no-drag;
  height: 100%;
  width: 138px;
  margin-left: auto;
}

.monaco-workbench.fullscreen > .part.titlebar > .window-controls-container {
  display: none;
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  > .window-icon-bg {
  display: inline-block;
  -webkit-app-region: no-drag;
  height: 100%;
  width: 33.34%;
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  .window-icon
  svg {
  shape-rendering: crispEdges;
  text-align: center;
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  > .window-icon-bg
  > .window-icon {
  height: 100%;
  width: 100%;
  -webkit-mask-size: 23.1%;
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  > .window-icon-bg:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.monaco-workbench
  > .part.titlebar.light
  > .window-controls-container
  > .window-icon-bg:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  > .window-icon-bg.window-close-bg:hover {
  background-color: rgba(232, 17, 35, 0.9);
}

.monaco-workbench
  > .part.titlebar
  > .window-controls-container
  .window-icon.window-close:hover {
  background-color: white;
}

/* Menubar styles */

.monaco-workbench .menubar {
  display: flex;
  flex-shrink: 1;
  box-sizing: border-box;
  height: 30px;
  -webkit-app-region: no-drag;
  overflow: hidden;
  flex-wrap: wrap;
  font-size: 13px;
}

.monaco-workbench.fullscreen .menubar {
  margin: 0px;
  padding: 0px 5px;
}

.monaco-workbench .menubar > .menubar-menu-button {
  align-items: center;
  box-sizing: border-box;
  padding: 4px 12px;
  cursor: pointer;
  -webkit-app-region: no-drag;
  zoom: 1;
  white-space: nowrap;
  border-radius: 2px;
}

.monaco-workbench .menubar .menubar-menu-items-holder {
  position: absolute;
  left: 0px;
  opacity: 1;
  z-index: 2000;
}

.monaco-workbench .menubar .menubar-menu-items-holder.monaco-menu-container {
  font-family: Inter, sans-serif;
  outline: 0;
  border: none;
  font-size: 0.75rem;
}

.monaco-workbench
  .menubar
  .menubar-menu-items-holder.monaco-menu-container
  :focus {
  outline: 0;
}
